* {
    margin: 0;
    padding: 0;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #202632;
    height: 100vh;
}

.advice-card {
    display: flex;
    align-items: center;
    width: 500px;
    height: 250px;
    background-color: #313a49;
    border-radius: 8px;
}

.advice {
    margin: 0 auto;
    user-select: none;
}

.advice h6 {
    position: relative;
    bottom: 40px;
    text-align: center;
    font-size: 15px;
    color: #55ffae;
    font-weight: 100;
}
.advice h3 {
    text-align: center;
    font-size: 23px;
    color: #ffff;
    font-weight: 500;
    padding: 5px;
}



.advice-card .circle {
    position: absolute;
    right: 49%;
    bottom: 35%;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #55ffae;
    cursor: pointer;
}
.advice-card .pause {
    position: relative;
    right: 50%;
    top: 30%;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #55ffae;
    cursor: pointer;
}

.advice-card .pause::before {
    content: "";
    position: absolute;
    display: block;
    background-color: white;
    top: 1px;
    left: 362px;
    margin-left: 30px;
    width: 165px;
    height: 1px;
}
.advice-card .pause::after {
    content: "";
    position: absolute;
    display: block;
    background-color: white;
    width: 165px;
    right: -386px;
    margin-right: 30px;
    top: 1px;
    gap: 10px;
    height: 1px;
}


@media (max-width: 767px) {
    .advice-card .circle {
        right: 45%;
        bottom: 33%;
    }
}